import React, { useState, useEffect, useContext } from 'react';
import {FormControl, InputLabel, makeStyles, MenuItem, Select} from '@material-ui/core'

import {DataContext} from '../../dataContext'

import Loading from './Loading'

import { useParams} from 'react-router-dom'
import ResultsBox from './ResultsBox';

const styles = makeStyles({
    root: {
        width: '100%',
        display: 'flex',
        height: '100%',
        overflow: 'hidden',
        flexDirection: 'column',
        fontFamily: 'Arial'
    },
    title: {
        background: '#9e9e9e',
        padding: 10, 
        fontSize: 24,
        color: 'white'
    },
    selectContainer: {
        width: 200,
        padding: 20
    },
    formControl: {
        width: '100%',
    }, 
    main: {
        overflow: 'auto', 
        flexGrow: 1
    },
    resultsContainer: {
        overflowY: 'auto',
        overflowX: 'hidden'
        
    }
})

function FilteredResultsApp() {
    const {data} = useContext(DataContext)
    const [filter, setFilter] = useState(null)
    const [results, setResults] = useState(null)
    const [show, setShow] = useState(false)
    // const [loading, setLoading] = useState(true)
    const classes = styles()
    let {prov, year, loadfilter} = useParams()

    const handleChange = (value) => {
        setFilter(value);
        // setLoading()
    }

    const getData = (list) => {
        console.log('getData', list)
        if (list.length > 0) {
            let requests = list.map(el=>fetch(`data/${prov}/data/${year}/filtered/${el.filename}`).then(res=>res.json()));
            Promise.all(requests).then(res=>{
                console.log('res',res)
                res.forEach((el,i)=>{
                    setResults(state=>({...state, [`${list[i].label}`]: el}))
                })
            })
    
        }

    }

    useEffect(()=> {
        console.log('get Results', data.config)
        let interval;
        if (data.config) {
            getData(data.config.filteredResults)

            setInterval(()=> {
                getData(data.config.filteredResults)
            }, data.config.timer)

            if (loadfilter) {
                const findFilter = data.config.filteredResults.find(el=>el.urlSlug.toLowerCase() === loadfilter.toLowerCase())
                console.log('test?',findFilter)
                if (findFilter) {
                    console.log(findFilter.label)
                    handleChange(findFilter.label)
                }
            }
        }
        return () => {
            clearInterval(interval)
        }
    }, [data.config])


    const GetResults = () => {
        console.log('here',results[filter])
        if (results[filter]) {
            return (
                <ResultsBox results={results[filter] && results[filter]} filter={filter}/>
            )
        } else {
            return (
                <p style={{paddingLeft: 20, }}>Select Filter to view Results</p>
            )
        }
        
    }


    if (!data.config) {
        return (
            <Loading />
        )
    } else {
        return (
            <div className={classes.root}>
                <div className={classes.title}>Filtered Results</div>
    
                {results ? 
                <>
                    <div className={classes.selectContainer}>
                            <FormControl className={classes.formControl}>
                            <InputLabel shrink={filter ? true : false}>Filter</InputLabel>
                            <Select 
                                value={filter} 

                                onChange={(e) => handleChange(e.target.value)}>
                                {data.config.filteredResults.map(filter=>{
                                    return (
                                        <MenuItem value={filter.label}>
                                            {filter.label}
                                        </MenuItem>
                                    )
                                })
                                }
                            </Select>
                        </FormControl>
                    </div>  
                    <div className={classes.resultsContainer}>
                        {results && <GetResults/>}
                    </div>
                </>
                :
                <div style={{padding: 20}}>
                    Filtered results not available for current election
                </div>}
            </div>
        )
    }

}
export default FilteredResultsApp
