import React, {useEffect, useReducer, createContext, useState} from 'react'

import {useLocation, useParams} from 'react-router-dom'

export const DataContext = createContext(null);

const initialData = {full: null,
    party: null,
    declaration: null,
    config: null,
    counter: 0}

const DataContextProvider = (props) => {
    // const [data, dispatch] = useReducer(dataReducer, initialData)
    const [data, setData] = useState(initialData)
    const {prov, year} = useParams();
    
    const getStaticData = () => {
        let eYear = year ? year : "2020"
        let eProv = prov?prov : "nb"

        Promise.all([
            fetch(`data/${eProv}/data/${eYear}/${eProv}_ridingResults.json`)
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching riding results')
                }),
            fetch(`data/${eProv}/data/${eYear}/${eProv}_partyResults.json`)  //http://electorfederal.blcloud.net/api/party/result/overall/(all)/json
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching party results')
                }),
            fetch(`data/${eProv}/data/${eYear}/${eProv}_declaration.json`)  // //http://electorfederal.blcloud.net/api/OverallResult/?format=json
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching declaration results')
                }),
            fetch(`data/${eProv}/data/${eYear}/${eProv}_config.json`)  // //http://electorfederal.blcloud.net/api/OverallResult/?format=json
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('Please add election config')
                }),
            // fetch(`data/${eProv}/data/${eYear}/${eProv}_prevResults`)
            
            
            ])
            .then(res=>{
                setData(prevData=>({
                    ...prevData,
                    type: 'SET_INITIAL', 
                    full: res[0], 
                    party: res[1],
                    declaration: res[2],
                    config: res[3] 
                }))
            }).catch(e=>{
                console.log('error', e)
            })
    }

    const getDynamicData = () => {

        let eYear = year ? year : "2020"
        let eProv = prov?prov : "nb"

        console.log('fetching data')
        Promise.all([
            fetch(`data/${eProv}/data/${eYear}/${eProv}_ridingResults.json`, {cache:"no-cache"})
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching riding results')
                }),
            fetch(`data/${eProv}/data/${eYear}/${eProv}_partyResults.json`, {cache:"no-cache"})  //http://electorfederal.blcloud.net/api/party/result/overall/(all)/json
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching party results')
                }),
            fetch(`data/${eProv}/data/${eYear}/${eProv}_declaration.json`, {cache:"no-cache"})  // //http://electorfederal.blcloud.net/api/OverallResult/?format=json
                .then(res=>{
                    return res.json();
                })
                .then(json=>{
                    return json
                })
                .catch(e=>{
                    alert('error fetching declaration results')
                })
            ])
            .then(res=>{
                setData(prevData=>({
                    ...prevData,
                    type: 'SET_INITIAL', 
                    full: res[0], 
                    party: res[1],
                    declaration: res[2]
                }))
            }).catch(e=>{
                console.log('error', e)
            })
    }

    useEffect(()=> {
        console.log('data context');
        getStaticData()
    }, [])

    useEffect(()=> {
        let interval;
        if (data.config) {
            console.log('updating every ' + (data.config.timer ? data.config.timer : 30000) + 'ms');
            interval = setInterval(()=>{
                getDynamicData();
                
            },(data.config.timer ? data.config.timer : 30000))
        }
        return () => {
            clearInterval(interval)
        }
    }, [data.config])   

    return (
        <DataContext.Provider value={{data}}>
            {props.children}
        </DataContext.Provider>
    )
}

export default DataContextProvider