import React, { Suspense, useState } from "react";
import "./App.css";

import {
	HashRouter as Router, 
	Switch, 
	Route, 
	Link,
	useRouteMatch,
	useParams
  } from "react-router-dom";

import {
  FormControl,
  InputLabel, 
  Select, 
  MenuItem
} from '@material-ui/core'

//Components

import SeatApp from './components/SeatApp/App' 
import GraphApp from './components/GraphApp/App'
import BarchartApp from './components/BarchartApp/App'
import FilteredResultsApp from "./components/FilteredResultsApp/App";

import DataContextProvider from './dataContext'

const MapApp = React.lazy(()=>import('./components/MapApp/App'));

const InstantArticleApp = React.lazy(()=>import('./components/InstantArticle/InstantArticleApp'));

const App = () => {

	return (
		<Router>
		<Switch>
		  <Route exact path="/">
			  <Home />
		  </Route>
		  <Route path="/map/:prov/:year/:riding?">
        <Suspense fallback={<div>Loading..</div>}>
			    <DataContextProvider>
  			    <MapApp showBanner={true}/>
			    </DataContextProvider>
        </Suspense>
		  </Route>
		  <Route path="/seat/:prov/:year?">
			<DataContextProvider>
			  <SeatApp />
			</DataContextProvider>
		  </Route>
		  <Route path="/graph/:prov/:year?">
			<DataContextProvider>
			  <GraphApp />
			</DataContextProvider>
		  </Route>
		  <Route path="/barchart/:prov/:year?">
			<DataContextProvider>
			  <BarchartApp />
			</DataContextProvider>
		  </Route>
		  <Route path="/filteredResults/:prov/:year/:loadfilter?">
			<DataContextProvider>
			  <FilteredResultsApp />
			</DataContextProvider>
		  </Route>
		  <Route path="/instantarticle/:prov/:year?">
			<DataContextProvider>
			  <InstantArticle />
			</DataContextProvider>
		  </Route>
		</Switch>
	  </Router>
	);
}

const Home = () => {
  const [prov, setProv] = useState({prov: 'nb', year: 2020});

	return (
		<div>
      <div className="electionSelect">
        <FormControl className="electionSelect">
          <InputLabel id="demo-simple-select-label">Select Election</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={prov}
            onChange={(e)=>setProv(e.target.value)}
          >
            {window.CONFIG.elections.map((el)=>{
              return <MenuItem value={el}>{el.prov} - {el.year}</MenuItem>
            })}
          </Select>
        </FormControl>
      </div>
      <div>
        <ul>
          <li>
            <Link to={`/`}>Home</Link>
          </li>
          <li>
            <Link to={`/map/${prov.prov}/${prov.year}`}>Map App</Link>
          </li>
          <li>
            <Link to={`/barchart/${prov.prov}/${prov.year}`}>Barchart App</Link>
          </li>
          <li>
            <Link to={`/graph/${prov.prov}/${prov.year}`}>Graph App</Link>
          </li>
          <li>
            <Link to={`/seat/${prov.prov}/${prov.year}`}>Seat App</Link>
          </li>
          <li>
            <Link to={`/instantarticle/${prov.prov}/${prov.year}`}>Instant Article</Link>
          </li>
          <li>
            <Link to={`/filteredResults/${prov.prov}/${prov.year}`}>Playlist Results</Link>
          </li>
        </ul>
        </div>
    	</div>
	)
}
const InstantArticle = () => {
	return (
		<div style={{display: "flex", flexDirection: 'column', height: '100%'}}>
			<div style={{width: '100%'}}>
				<InstantArticleApp />
			</div>
			<div style={{flexGrow: '1', width: '100%', position: 'relative', overflow: 'auto'}}>
				<MapApp showBanner={false}/>
			</div>
				{/* <InstantArticleApp />
				<MapApp /> */}
		</div>
	)
}
export default App;