import React, {useState, useEffect, useContext} from 'react'
import {makeStyles} from '@material-ui/core'

import {DataContext} from '../../dataContext'
import MajorityMeter from './MajorityMeter';

const styles = makeStyles({
	barchartApp: {
		height: 250,
		position: "relative",
	},
	main: {
		// fontFamily: 'Roboto',
		height: '100%',
		display: 'flex',
		position: 'relative',
		flexDirection: 'column',
		padding: '10px 10px',
		backgroundColor: '#ededf0'
	},
	titleRow: {
		flex:2,
		display: 'flex', 
		padding: '0px 5px 5px 5px',
		justifyContent: 'space-between'
	},
	title: {
		fontSize: 24,
		// paddingBottom: 10, 
		fontWeight: 'bold'
	},
	declarationText: {
		paddingTop: 5,
		'& #party' : {
			fontWeight: 'bold'
		},
		'& #majority': {
			fontSize: 14
		}
	},
	barchart: {
		fontSize: 14,
		display: 'flex',
		padding: '15px 0px 10px 0px',
		flexDirection: 'column-reverse',
		flexWrap: 'wrap',
		flex:3, 
	},
	partyMap: {
		fontSize: 16,
		display: 'flex',
		flex: 2,
		alignContent: 'left',
		overflowX: 'auto',
		maxHeight: 52
	},
	update: {
		fontSize: 10,
		padding: '5px 0px 0px 5px'

    },
    partyContainer: {
        width: '20%',
		paddingTop: 5,
		display: 'flex',
        alignItems: 'center',
        fontWeight: 'bold',
		fontSize: 14,
        letterSpacing: 2,
        padding: '5px 5px',
        margin: 1,
        borderRadius: 5,
        color: 'white',
        justifyItems: 'center',
		justifyContent: 'space-between',
    },
    popVote:{
        // paddingTop: 5,
        fontWeight: 'bold',
        fontSize: 14
    }
})


export default function App() {
    const {data} = useContext(DataContext)
    const classes = styles()
	const [parties, setParties] = useState([])
    
    useEffect(()=> {
        if (data.party) {
			let total = 0
			setParties([])
            data.party.partyResults.map((party, i)=>{
				setParties(oldArray=>[...oldArray, {partyName: party.nameShort,id:i, seats: party.seats, color: party.color,votesPercent: party.votesPercent,votesTotal: party.votes}])
				
			})
		}
		// if (data.declaration) {
		// 	if (data.declaration.overallResult.resultText) {
		// 		let text = data.declaration.overallResult.partyName + ' ' + data.declaration.overallResult.resultText;
		// 		setDeclaration(text)	
		// 	}
		// }
    }, [data])

    const getDate = (update) => {
		const date = new Date(update)
		return date.toString().split(' ').slice(1, 5).join(' ')

	}

    const Party = (props) => {
		const {party} = props
		return (
			<div key={party.id} id="partyCard" className={classes.partyContainer} style={{backgroundColor: party.color}}>
				<div>{party.partyName}</div>
				<div id="popVote" className={classes.popVote}> {party.seats} </div>
			</div>
		);
	}

    const DeclarationText = () => {
		let winner;
		let leading = false;
		let declaration;
        if (data.party.partyResults[0].seats > data.party.partyResults[1].seats) {
			winner = data.party.partyResults[0].name
			leading = true
        } else {

		}

		if (data.declaration.overallResult.resultText) {
			declaration = 'elected ' + data.declaration.overallResult.resultText
		}

		return (
            <div id="declarationText" className={classes.declarationText}>
                {leading && <>
					<span id="party">{winner}s</span>
					<span id="helpertext"> leading </span> 
					{declaration &&
						<span>and elected</span>
					}
					<span> - </span>
				</>
				}
				<span id="majority">{data.config.majority} seats needed for majority</span>
            </div>
        )
	}
    return (
        <div className={classes.barchartApp}>
            {data.party && 
             <div className={classes.main}>
                <div className={classes.titleRow}>
					<div className={classes.left}>
						<div id="widgetTitle" className={classes.title}>{data.config.title}</div>
						<DeclarationText />
					</div>
				</div>
                <div className={classes.barchart}>
                    <MajorityMeter seatTotal={data.config.seats} majority={data.config.majority} majorityPercent={(data.config.majority/data.config.seats)*100} data={data.party}/>
                </div>
                
                <div className={classes.partyMap}>
                    {parties.map(party=>{
						return <Party key={party.id} party={party}/>
					})}
                </div>
                <div className={classes.update}>Last updated: {getDate(data.party.generated)}</div>
            </div>
            }
        </div>
    )
}
